.apology-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 160px;
		transform: translateX(-40%);
		@include max-md {
			width: 140px;
		}
		img {
			transform-origin: 100% 100%;
		}
	}
	.appology-wrap {
		position: relative;
		padding: 3rem 2rem;
		background-color: var(--color-primary);
		border-radius: var(--border-radius);
		@include max-md {
			padding: 2rem 1rem;
		}
		.background-element {
			img {
				border-radius: var(--border-radius);
			}
		}
	}
}
