.thank-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 140px;
		transform: translateX(-40%);
		@include max-md {
			width: 120px;
		}
		img {
			transform-origin: 100% 100%;
		}
	}
	.orn-2 {
		position: absolute;
		bottom: 5rem;
		right: 0;
		width: 180px;
		transform: translateX(40%);
		@include max-md {
			width: 160px;
		}
		img {
			transform-origin: 0% 100%;
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 3/4;
		padding: 8px;
		.thank-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
			border-radius: 1000px;
			position: relative;
		}
	}
	.thank-wrapper {
		position: relative;
		border-radius: var(--border-radius);
		.thank {
			position: relative;
			padding: 3rem 2rem;
			height: 100%;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			z-index: 5;
			@include max-md {
				padding: 2rem 1rem;
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
