.header-section {
	position: relative;
	height: 100vh;
	width: 100%;
	@include max-lg {
		height: auto;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 120px;
		transform: translateX(-30%);
		img {
			transform-origin: 100% 100%;
		}
	}
	.orn-2 {
		position: absolute;
		bottom: 3rem;
		right: 0;
		width: 160px;
		transform: translateX(30%);
		img {
			transform-origin: 0% 100%;
		}
	}
	.scroll-icon {
		position: absolute !important;
		top: 100%;
		left: 50%;
		transform: translate(-50%, 1rem);
		z-index: 5;
		@include max-lg {
			transform: translate(-50%, 0.5rem);
		}
		@include max-md {
			top: 0;
			transform: translate(-50%, -130%);
		}
	}
	.elements-absolute {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		.background-element {
			display: block;
			height: 100%;
			.background-wrapper {
				width: 100%;
				height: 100%;
				display: inline-block;
				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 3/4;
		@include max-md {
			margin-top: 2rem;
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			padding: 8px;
			.header-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: 1000px;
			}
		}
	}
	.header-content {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		.header {
			position: relative;
			display: flex;
			padding: 4rem 0;
			justify-content: center;
			align-items: center;
			flex-wrap: wrap;
			.header-title {
				position: relative;
				text-align: center;
				&-content {
					position: relative;
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}
