.countdown {
	display: grid;
	grid-template-columns: 1fr 1fr;
	align-items: center;
	justify-items: center;
	text-align: center;
	position: relative;
	margin: 0 auto;
	width: fit-content;
	gap: 1rem;
	border-radius: 5px;
	z-index: 3;
	padding-bottom: 0.5rem;
	@include max-lg {
		display: flex;
		justify-content: space-evenly;
		gap: 0.5rem;
	}
	@include max-xs {
		gap: 0.25rem;
	}
	.days,
	.hours,
	.minutes,
	.seconds {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		position: relative;
		background-color: transparent;
		border-radius: 1000px;
		border: 2px solid var(--border-color);
		padding: 1rem 0.5rem;
		width: 78px;
		@include max-md {
			width: 65px;
		}
		.huruf {
			font-size: 14px;
			font-weight: 600;
		}
		.angka {
			margin-bottom: 0;
			font-weight: 500;
			font-size: 28px;
			font-family: $font-secondary;
		}
		.huruf,
		.angka {
			margin: 0;
			padding: 0;
			line-height: normal;
		}
	}
}
