.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	height: 100vh;
	width: 100%;
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 120px;
		transform: translateX(-30%) rotate(-20deg);
		img {
			transform-origin: 100% 100%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 160px;
		img {
			transform-origin: 0% 100%;
		}
	}
	.cover-wrapper {
		position: relative;
		padding: 4rem 0;
		@include max-lg {
			padding: 4rem 0 8rem;
			align-items: center;
		}
		.cover-content {
			text-align: center;
			position: relative;
			color: var(--color-dark);
			height: 100%;
			display: flex;
			justify-content: center;
			align-items: center;
			.cover-body {
				position: relative;
				display: flex;
				justify-content: center;
				align-items: center;
				@include max-lg {
					flex-direction: column;
				}
				@include max-md {
					display: grid;
					grid-template-rows: auto 1fr;
				}
				@include max-mhc(667px) {
					justify-content: flex-start;
				}
				.greeting-wrapper {
					position: relative;
					@include max-md {
						font-size: 14px;
					}
					h6 {
						font-weight: 700;
					}
				}
				.image-wrapper {
					position: relative;
					width: 100%;
					height: auto;
					aspect-ratio: 3/4;
					padding: 8px;
					@include max-md {
						width: 80%;
						margin: 0 auto;
					}
					@include max-mhc(690px) {
						height: 100%;
						width: 80%;
						aspect-ratio: 1;
					}
					.cover-background {
						width: 100%;
						height: 100%;
						object-fit: cover;
						border-radius: 1000px;
					}
				}
				.cover-description {
					padding: 3rem 1rem;
					position: relative;
					@include max-lg {
						padding: 2rem 1rem 1rem;
					}
					&::before {
						position: absolute;
						top: 0;
						right: 0;
						width: 120%;
						height: 100%;
						border-radius: var(--border-radius);
						border: 2px solid var(--border-color);
						content: "";
						z-index: -1;
						@include max-lg {
							width: 100%;
							height: calc(100% + 10rem);
							top: unset;
							bottom: 0;
						}
					}
				}
			}
			.elements-widget {
				width: 100%;
			}
		}
	}
}
