.stream-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		right: 0;
		width: 160px;
		transform: translateX(40%) scaleX(-1);
		@include max-md {
			width: 120px;
			top: unset;
			bottom: 5rem;
		}
		img {
			transform-origin: 100% 100%;
		}
	}
	.stream-wrapper {
		position: relative;
		border-radius: var(--border-radius);
		z-index: 5;
		@include max-lg {
			.row {
				align-items: center;
				gap: 2rem;
			}
		}
		.stream-content {
			position: relative;
			padding: 3rem 2rem;
			border-radius: var(--border-radius);
			background-color: var(--color-primary);
			@include max-md {
				padding: 2rem 0.75rem;
			}
			h4 {
				@include max-md {
					text-align: center;
				}
			}
		}
	}
	ul {
		ol {
			padding: 0;
		}
	}
	.card {
		background-color: transparent;
		height: 100%;
		color: var(--color-dark);
		border: none;
	}
	.btn-custom {
		width: 100%;
		z-index: 10;
		position: relative;
	}
	.yt-embed {
		@include min-lg {
			height: 320px;
		}
	}
	.yt-description {
		width: 100%;
		margin-top: 3rem;
		@include max-md {
			text-align: center;
		}
	}
}
