.venue-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 160px;
		transform: translate(-40%, 0);
		@include max-lg {
			width: 140px;
		}
		@include max-md {
			width: 120px;
		}
		img {
			transform-origin: 100% 100%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 140px;
		transform: translate(50%, 0) scaleX(-1);
		@include max-lg {
			width: 120px;
		}
		img {
			transform-origin: 100% 100%;
		}
	}
	.elements-absolute {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		.background-element {
			display: block;
			height: 100%;
			.background-wrapper {
				width: 100%;
				height: 100%;
				display: inline-block;
				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
					border-radius: var(--border-radius);
				}
			}
		}
	}
	.venue-wrapper {
		position: relative;
		z-index: 3;
		.venue-description {
			position: relative;
			margin: 0 auto 5rem;
			z-index: 2;
			text-align: center;
			@include max-lg {
				margin-bottom: 3rem;
			}
		}
		.venue-content {
			position: relative;
			display: flex;
			flex-direction: column;
			gap: 3rem;
			.ribbon-venue {
				position: absolute;
				position: absolute;
				top: 0;
				left: 50%;
				transform: translate(-50%, -50%);
				padding: 5px;
				.border-custom {
					border-width: 1px;
				}
				.icon-element {
					position: relative;
					background-color: var(--color-primary);
					padding: 1rem 0.5rem;
					border-radius: 50px;
					svg {
						width: 40px;
						height: auto;
						path {
							stroke: var(--color-dark);
						}
					}
				}
			}
			.card {
				border: none;
				background-color: var(--color-primary);
				height: 100%;
				border-radius: var(--border-radius);
				z-index: 5;
				text-align: center;
				.card-body {
					padding: 2rem;
					@include max-lg {
						padding: 2rem 1rem;
					}
					h4 {
						font-weight: 600;
						margin: 1rem 0;
					}
				}
			}
		}
	}
	.prokes-wrapper {
		margin-top: 3rem;
		position: relative;
		padding: 2rem;
		border-radius: 10px;
		text-align: center;
		z-index: 2;
		@include max-md {
			padding: 2rem 0;
		}
		* {
			position: relative;
		}
	}
	.prokes-wrap {
		display: flex;
		gap: 0.75rem;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		text-align: center;
		.prokes {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			width: 132px;
			@include max-md {
				width: 120px;
			}
			@include max-xs {
				width: 45%;
			}
		}
	}
	.row-venue {
		@include max-md {
			gap: 5rem;
		}
	}
	.event-name {
		position: relative;
		h4 {
			font-family: $font-secondary;
		}
		.date {
			position: relative;
			display: flex;
			justify-content: center;
			gap: 0.5rem;
			text-align: left;
			border-top: 1px solid var(--color-dark);
			border-bottom: 1px solid var(--color-dark);
			width: fit-content;
			height: 100%;
			margin: 0.5rem auto 0;
			h5 {
				font-weight: 700;
				margin-bottom: 0;
				font-size: 38px;
			}
			p {
				margin-top: 0.2rem;
				font-size: 12px;
				font-weight: 400;
			}
			.date-line {
				border-left: 1px solid var(--color-dark);
				width: 1px;
				height: 45px;
				border-radius: 20px;
				content: "abc";
			}
		}
		p {
			margin-bottom: 0;
			font-weight: 600;
		}
	}
	.event-place {
		display: block;
		margin-top: 2rem;
	}
	.btn-custom {
		background-color: transparent;
		border-color: var(--color-button-trans);
		color: var(--color-button-trans);
		transition: all 0.25 ease-in-out;
		&:hover {
			background-color: var(--color-secondary) !important;
			border-color: var(--color-secondary);
			color: var(--text-button);
		}
	}
	.animation-loop {
		.st1 {
			will-change: transform;
			animation: zoomInRing 2000ms ease-out infinite;
		}
		.dot {
			will-change: transform;
			animation: fadeBoing 2000ms ease-out infinite;
			transform-origin: 50% 75%;
		}
		#Layer_3 {
			will-change: transform;
			animation: boing 1000ms ease-out infinite alternate;
		}
	}

	@keyframes zoomInRing {
		0% {
			opacity: 0;
			transform: scale3d(0.5, 0.5, 0.5);
		}
		40% {
			opacity: 1;
		}
		70% {
			opacity: 0;
			transform: scale3d(1.25, 1.25, 1.25);
		}
		100% {
			opacity: 0;
		}
	}

	@keyframes boing {
		0% {
			transform: translate3d(0, 0, 0);
		}
		100% {
			transform: translate3d(0, -5rem, 0);
		}
	}

	@keyframes fadeBoing {
		0% {
			opacity: 1;
		}
		50% {
			transform: scale3d(0.25, 0.25, 0.25);
			opacity: 0;
		}
		100% {
			transform: scale3d(1, 1, 1);
			opacity: 1;
		}
	}
}

.maps-embed {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
}
