.rsvp-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 180px;
		transform: translate(-20%, -10%);
		img {
			transform-origin: 100% 100%;
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 3/4;
		padding: 8px;
		.rsvp-background {
			position: relative;
			width: 100%;
			height: 100%;
			object-fit: cover;
			border-radius: 1000px;
		}
	}
	.rsvp-wrapper {
		position: relative;
		border-radius: var(--border-radius);
		.rsvp-form {
			padding: 2rem;
			position: relative;
			@include max-lg {
				padding: 2rem 1rem;
			}
		}
	}
}
