.couple-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		left: 0;
		top: 60%;
		width: 140px;
		transform: scaleX(-1) translateX(50%);
		img {
			transform-origin: 100% 100%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		left: 0;
		width: 140px;
		transform: translateX(-30%);
		img {
			transform-origin: 100% 100%;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 12rem;
		right: 0;
		width: 160px;
		transform: translateX(40%);
		@include max-lg {
			width: 140px;
			bottom: unset;
			top: 3rem;
		}
		img {
			transform-origin: 0% 100%;
		}
	}
	.overlay-bottom {
		border-radius: 0 0 var(--border-radius) var(--border-radius);
	}
	.elements-wrapper-absolute {
		overflow: hidden;
		border-radius: var(--border-radius);
	}
	.and {
		@include min-md {
			position: absolute;
			top: 0;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		font-weight: 600;
		color: var(--color-dark);
		display: flex;
		border-radius: 1000px;
		padding: 1rem;
		border: 2px solid var(--border-color);
		justify-content: center;
		align-items: center;
		width: fit-content;
		position: relative;
		z-index: 3;
		span {
			position: relative;
			font-size: 24px;
			font-family: $font-secondary;
		}
	}
	.couple-content {
		position: relative;
		margin-bottom: 10rem;
	}
	.couple {
		position: relative;
		text-align: center;
		padding: 1rem 0;
		background-color: var(--color-primary);
		border-radius: var(--border-radius);
		height: 100%;
		z-index: 5;
		.image-wrap {
			width: 80%;
			position: relative;
			aspect-ratio: 3/4;
			margin: -10rem auto 0;
			padding: 8px;
			&.woman {
				@include max-md {
					margin-top: 2rem;
				}
			}
			.couple-image {
				height: 100%;
				object-fit: cover;
				position: relative;
				width: 100%;
				border-radius: 1000px;
			}
		}
		.couple-description {
			position: relative;
			padding: 1rem;
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: 100%;
			.sosmed {
				color: var(--color-button-trans);
				text-decoration: none;
				padding: 0.5rem 1.5rem;
				border-radius: 50px;
				background: transparent;
				border: 1px solid var(--color-dark);
				transition: all 0.25s ease;
				display: block;
				width: fit-content;
				margin: 0 auto;
				&:hover {
					background-color: var(--color-secondary);
					border: 1px solid var(--color-secondary);
					color: var(--text-button);
				}
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
	.reminder-wrap {
		padding: 3rem 0 5rem;
		@include max-md {
			padding: 3rem 0;
		}
		.elements-wrapper-absolute {
			height: calc(100% + 5rem);
		}
	}
}
