.story-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		right: 0;
		top: 0;
		width: 180px;
		transform: translate(-5%, -10%);
		@include max-md {
			width: 160px;
			transform: translate(10%, -10%);
		}
		img {
			transform-origin: 0% 100%;
		}
	}
	.orn-2 {
		position: absolute;
		right: 0;
		bottom: 0;
		width: 140px;
		transform: translate(50%, 0);
		@include max-md {
			width: 120px;
			bottom: -8rem;
		}
		img {
			transform-origin: 100% 100%;
		}
	}
	.story-content {
		position: relative;
		border-radius: var(--border-radius);
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		align-items: center;
		margin-top: 3rem;
	}
	.story-description {
		position: relative;
		text-align: center;
		* {
			position: relative;
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		aspect-ratio: 16/9;
		border-radius: 100%;
		z-index: 3;
		@include max-lg {
			aspect-ratio: 4/3;
		}
		.story-background {
			object-fit: cover;
			width: 100%;
			height: 100%;
			border-radius: 1000px;
			position: relative;
		}
	}
}
